@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@200;300;400;500&display=swap");
* {
  font-size: 2rem;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
}

.calculator {
  background-color: rgb(57, 104, 158);
  box-shadow: 2px 2px;
  border-radius: 2rem;
  padding: 1rem;
  font-family: "IBM Plex Mono", monospace;
}
.calculator__title {
  color: #fff;
}

h4 {
  margin: 0;
  font-size: 1.5rem;
  vertical-align: middle;
}

span {
  display: inline-block;
  align-items: center;
  justify-content: center;
}

svg {
  width: 1.5rem;
}

.container {
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  margin: 0rem auto;
  width: 25.6rem;
  text-align: center;
  padding-left: 1rem;
  padding-right: 1rem;
  border: 2px solid #181818;
}
.container .display__input {
  width: 100%;
  height: 5rem;
  text-align: right;
  padding: 1.5rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  outline: none;
  border: 1px solid #181818;
  background-color: #fff;
  border-radius: 2rem;
}
.container input[type=text][disabled] {
  color: #181818;
}
.container .keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(4rem, auto);
  gap: 1rem;
  padding-top: 0;
}
.container .keypad button {
  font-family: "IBM Plex Mono", monospace;
  border: 1px solid #181818;
  box-shadow: 2px 2px #181818;
  border-radius: 3rem;
  background-color: white;
  transition: transform 0.2s;
}
.container .keypad button:active {
  transform: scale(0.98);
}
.container .keypad button.highlight {
  color: #fff;
  font-weight: 500;
}
.container .keypad button.highlight--1 {
  background-color: #292929;
}
.container .keypad button.highlight--2 {
  color: #fff;
  background-color: #ff5b5b;
}

#clear {
  grid-column: 1/3;
  grid-row: 1;
}

#result {
  grid-column: 3/5;
  grid-row: 5;
}